// This is a isomorphic JS. Can be used both on server / client.
// !!! --->>  DO NOT IMPORT  <<--- !!! "fs" / "path" / etc... Browsers do not have "fs" & "path" & etc...
import { StringHelper } from "./stringHelper";

export class PathHelper {
    public join(...paths: Array<string>): string {
        return paths.reduce((prev, path) => prev + "/" + StringHelper.trim(path, "/"), "");
    }
}

export const path = new PathHelper();